.main {
    text-align: left;
    border-radius: 20px;
    width: 380px;
    margin: -4px auto 28px auto;
    padding: 2% 0 5% 5%;
    box-shadow: 0px 4px 24px rgba(0, 0, 0, 0.1);
    -webkit-border-radius: 20px;
    -moz-border-radius: 20px;
    -ms-border-radius: 20px;
    -o-border-radius: 20px;
}

.row {
    display: flex;
    width: 90%;
    flex-direction: row;
}

.sub_total{
    padding-right: 42%;
}

.pickup_savings {
    width: 100%;
    color: black;
    text-decoration: underline;
    cursor: pointer;
}

.pickup_savings_value {
    margin-left: 26%;
    color: red;
}

.tax {
    width: 39%;
}

.tax_value {
    margin-left: 21%;
}

.total{
    padding-right: 32%;
}

hr.divider {
    margin: 16px 0;
}

.divider {
    border-color: #e6e7e8;
}

hr {
    display: block;
    height: 1px;
    border: 0;
    border-top: 1px solid #ccc;
    padding: 0;
    width: 80%;
}

.details_btn {
    border: none;
    padding-left: 0;
}

.details_btn:focus {
    outline: none;
}

#btn_text {
    text-decoration: underline;
    font-size: 16px;
}

#btn_symbol {
    font-size: 2.7em;
    font-weight: 200;
    padding-left: 20px;
}

.details_collapse{
    display: flex;
    flex-direction: row;
    margin-top: 20px;
}

.details{
    margin-left: 5%;
    width: 37%;
}

.price_detail{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding-right: 21%;
}

.strike_through {
    color: grey !important;
    font-weight: 600;
}

.discount_label{
    color: grey;
}

.discount_form{
    margin-top: 3%;
}

.discount_input {
    margin-top: 5%;
    margin-right: 5%;
    padding: 2% 7%;
    font-size: 16px;
}

.discount_input:focus {
    outline: none
}

.submit_btn{
    border: 2px solid black;
    border-radius: 15px;
    -webkit-border-radius: 15px;
    -moz-border-radius: 15px;
    -ms-border-radius: 15px;
    -o-border-radius: 15px;
    font-size: 16px;
    padding: 1%;
    margin-left: 2%;
}

